.product-search {
    width: 80%;
    border: solid black 2px;
    border-radius: 20px;
    display: flex; 
    height: 35px;
    margin-left: 120px;
    padding:10px;
    margin-top:90px;
}
.product-search input{
    border:none;
    background-color: transparent;
    width:100%;
}
.product-search input:focus{
    outline: none;
}
h2{
    text-align: center;
}
body{
    background-color: #e2e2e2;
    background: linear-gradient(to right, #e2e2e2, #e4ec92);
}
.btn-color{
    background-color: #cada12;
    color:white;
    border-radius: 5px;
    width:50px;
    font-weight: 400;
    border:none ;
    height: max-content;
    cursor: pointer;
}