.product-search {
    width: 80%;
    border: solid black 2px;
    border-radius: 20px;
    display: flex; 
     
    margin-left: 120px;
    padding:10px;
    margin-top:90px;
}
.product-search input{
    border:none;
    background-color: transparent;
    width:100%;
}
.product-search input:focus{
    outline: none;
}
h2{
    text-align: center;
}
body{
    background-color: #e2e2e2;
    background: linear-gradient(to right, #e2e2e2, #e4ec92);
}