@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #cada00; /* Navbar background color */
  color: white;
  position: fixed;
  top: 0px; /* Initially hidden */
  width: 100%;
  transition: top 0.3s; /* Smooth transition for sliding */
  padding: 10px;
  box-shadow: rgba(196, 201, 118, 0.25) 0px 30px 60px -12px inset,
              rgba(201, 231, 118, 0.3) 0px 18px 36px -18px inset; 
  z-index: 1000; /* Ensure it stays on top */
}

.logo img {
  margin-left: 10px;
  width: 250px;
  height: 45px;
  border-radius: 0%; /* No border radius */
  border: none; /* No border */
}

.nav-links {
  display: flex;
  align-items: center;
  column-gap: 20px; 
}

.nav-links p, a {
  color: white; /* Link color */
  text-decoration: none; /* No underline */
  font-weight: 600; /* Bold text */
  font-size: large; /* Font size */
  transition: color 0.3s; /* Smooth color transition */
  padding: 10px; /* Padding around links */
}

.nav-links a:hover {
  cursor: pointer; /* Change cursor on hover */
  color: #ffffff; /* Optionally change link color on hover */
}

/* Optional: Adjust navbar when scrolled */
.header.scrolled {
  top: 0; /* Show navbar when scrolled */
}

