.success-popup {
    position: fixed;
    top: 50%;
    left: 80%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background-color: white;
    border: 1px solid green;
    border-radius: 5px;
    width: max-content;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;
}
.form-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.btn-submit {
    background-color: #cada12;
    color: white;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 500;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 48%; /* Ensure buttons take up equal width */
    text-align: center;
}

.btn-submit:hover {
    background-color: #8e9441;
}