@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');
.img{
    background-image: url('../../Assets/homebg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: azure;
    height: 400px;
}
.home {
    padding-left:700px;
    padding-top:140px;
    padding-right: 7px;
   
  }
 
  .home-button {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 10px;
    background-color: #cada12;
    color: azure;
    cursor: pointer;
    border-radius: 10px;
}
.news {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}

.news input {
    width: 80vw;
    padding: 11px;
    margin-bottom: 10px;
    border: solid black 3px;
}

.news button {
    margin-top: 10px;
    color: white;
    background-color: #cada12;
    border-radius: 10px;
    padding: 10px;
}